:root {
  --accent-font-blue-color: #4f2ee8;
  --yellow-color: #eac645;
  --backg-overlay-modal: rgba(17, 17, 17, .4);
  --white-color: #fff;
  --black-color: #111;
  --main-txt-color: #111;
  --pagination-back: #111;
  --hovertxt: #4f2ee8;
  --btn-border: #4f2ee8;
  --pagination-next: #eac645;
  --header-background: #fff;
  --secondary-txt-color: #f3f3f3;
  --paragraph-color: #b4afaf;
  --paragraph-white-font-color: #f6f6f6;
  --background-body: #f6f6f6;
  --pagination-bkg: #f4f4f4;
  --shopping-list-border: rgba(79, 46, 232, .4);
  --category-name: rgba(17, 17, 17, .6);
  --scroll: #e0e0e0;
  --logo-txt: #000;
  --scroll-line: #f1f1f1;
  --modal-bgr: #fff;
  --avtoriz-form: #111;
  --avtoriz-modal-toggle: rgba(17, 17, 17, .5);
  --amazon-logo: grayscale(100%);
  --main-font: "DM Sans", sans-serif;
}

:root[data-theme="dark"] {
  --main-txt-color: #fff;
  --pagination-back: #111;
  --pagination-next: #eac645;
  --secondary-txt-color: #f3f3f3;
  --paragraph-color: #b4afaf;
  --hovertxt: #eac645;
  --btn-border: #4f2ee8;
  --header-background: #111;
  --category-name: rgba(255, 255, 255, .6);
  --background-body: #202024;
  --logo-txt: #f3f3f3;
  --shopping-list-border: rgba(79, 46, 232, .4);
  --scroll: rgba(17, 17, 17, .6);
  --modal-bgr: #202024;
  --avtoriz-form: #f6f6f6;
  --paragraph-white-font-color: #f6f6f6;
  --pagination-bkg: #f4f4f4;
  --avtoriz-modal-toggle: rgba(246, 246, 246, .5);
  --scroll-line: rgba(31, 31, 35, .6);
  --amazon-logo: brightness(0) invert(1);
}

body {
  max-width: 1440px;
  background-color: var(--background-body);
  margin: 0 auto;
  padding: 0;
  font-family: DM Sans, sans-serif;
  transition: background-color .3s;
}

a {
  color: inherit;
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.container {
  width: 100vw;
  outline-offset: -2px;
  margin-left: auto;
  margin-right: auto;
}

@media screen and (max-width: 767px) {
  .container {
    max-width: 375px;
    margin-bottom: 40px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 40px 32px 100px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    max-width: 1440px;
    flex-direction: row;
    align-items: start;
    padding-left: 24px;
    padding-right: 24px;
    display: flex;
  }
}

.header-container {
  overflow: initial;
  z-index: 999999999;
  height: 62px;
  border: 1.5px solid var(--main-txt-color);
  background-color: var(--header-background);
  border-radius: 0 0 8px 8px;
  align-items: center;
  padding: 0 16px;
  transition: background-color .3s;
  display: flex;
  position: relative;
  top: 0;
  left: 0;
}

