}

.fixed-header {
  z-index: 200;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.logo {
  color: var(--logo-txt);
  align-items: center;
  gap: 8px;
  font-weight: 700;
  display: flex;
}

@media screen and (max-width: 768px) {
  .site-nav {
    display: none;
  }
}

@media screen and (min-width: 768px) {
