.title-best-sellers {
  font-weight: 700;
  font-size: 32px;
  line-height: 1.19;
  letter-spacing: -0.04em;
  text-transform: capitalize;
  color: var(--main-txt-color);
  margin-bottom: 40px;
  /* margin-top: 40px; */
}

@media screen and (min-width: 768px) {
  .title-best-sellers {
    /* margin-top: 100px; */
    font-size: 48px;
    line-height: 1.08;
  }
}

/* @media screen and (min-width: 1440px) {
  .title-best-sellers {
    /* margin-top: 0px; 
  }
} */

.title-best-sellers-part {
  color: var(--accent-font-blue-color);
}

.name-category {
  font-weight: 400;
  font-size: 14px;
  line-height: 1.29;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  color: var(--paragraph-color);
  margin-bottom: 18px;
  display: block;
}

.list-category {
  display: flex;
  width: 335px;
  overflow: hidden;
}
@media screen and (min-width: 768px) {
  .list-category {
    width: 704px;
    gap: 25px;
    justify-content: space-between;
  }
}

@media screen and (min-width: 1440px) {
  .list-category {
    width: 996px;
    gap: 24px;
  }
}

.card-book {
  transition: transform 0.3s, box-shadow 0.3s;
}
.card-book:hover,
.card-book:focus {
  border-radius: 8px;
  transform: scale(1.01); /*    10% */
  box-shadow: 5px 5px 15px rgba(76, 75, 75, 0.3);
}

.img-card-book {
  display: block;
  width: 335px;
  height: 485px;
  margin-bottom: 14px;
}

@media screen and (min-width: 768px) {
  .img-card-book {
    width: 218px;
    height: 316px;
  }
}

@media screen and (min-width: 1440px) {
  .img-card-book {
    width: 180px;
    height: 256px;
  }
}

.img-book {
  width: 100%;
  height: 100%;
  overflow: hidden;
  border-radius: 8px;
}

.bestsellers-text-wrapper {
  margin-bottom: 8px;
}

@media screen and (min-width: 768px) {
  .bestsellers-text-wrapper {
    margin-bottom: 20px;
  }
}

@media screen and (min-width: 1440px) {
  .bestsellers-text-wrapper {
    margin-bottom: 32px;
  }
}

.tirle-wrap {
  width: 335px;
}

.author-wrap {
  width: 335px;
  height: 18px;
}

@media screen and (min-width: 768px) {
  .title-wrap,
  .author-wrap {
    width: 218px;
  }
}

@media screen and (min-width: 1440px) {
  .title-wrap,
  .author-wrap {
    width: 180px;
  }
}

.title-book {
  font-weight: 700;
  font-size: 16px;
  line-height: 1.12;
  letter-spacing: -0.04em;
  white-space: nowrap;
  overflow: hidden;
  text-transform: uppercase;
  text-overflow: ellipsis;
  color: var(--main-txt-color);
  margin-bottom: 4px;
}

.book-author {
  font-style: italic;
  font-weight: 400;
  font-size: 12px;
  line-height: 1.17;
  letter-spacing: -0.04em;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  padding-top: 4px;
  padding-bottom: 4px;
  color: var(--paragraph-color);
}

.see-more {
  cursor: pointer;
  display: block;
  font-family: var(--main-font);
  font-weight: 700;
  font-size: 14px;
  line-height: 1.29;
  letter-spacing: -0.01em;
  text-transform: uppercase;

  color: var(--main-txt-color);
  box-sizing: border-box;
  width: 124px;
  height: 46px;
  margin-left: auto;
  margin-bottom: 40px;
  background: transparent;
  border: 2px solid var(--accent-font-blue-color);
  border-radius: 40px;
  transition: color, background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.see-more:hover,
.see-more:focus {
  background: var(--accent-font-blue-color);
  color: var(--secondary-txt-color);
}

.link-books:hover {
  cursor: pointer;
}
