:root {
    
      /* COLORS */
/*    */
   --accent-font-blue-color: #4F2EE8;
   --yellow-color: #EAC645;
   --backg-overlay-modal: rgba(17, 17, 17, 0.4);
   --white-color: #fff;
   --black-color: #111111;

    --main-txt-color:  rgba(17, 17, 17, 1);
    --pagination-back: rgba(17, 17, 17, 1);
    --hovertxt: rgba(79, 46, 232, 1);
    --btn-border:rgba(79, 46, 232, 1);
  
    --pagination-next:rgb(234,198,69);
    --header-background: rgb(255, 255, 255);
    --secondary-txt-color: rgb(243, 243, 243);
    --paragraph-color: rgb(180, 175, 175);
    --paragraph-white-font-color: #f6f6f6;
    --background-body:  rgba(246, 246, 246, 1);
    --pagination-bkg: #f4f4f4;
    --shopping-list-border: #4f2ee866;
    --category-name: rgba(17, 17, 17, 0.6);
    --scroll: rgba(224, 224, 224,1);
    --logo-txt: #000000;
    --scroll-line: #f1f1f1;
    --modal-bgr: rgba(255, 255, 255, 1);
    --avtoriz-form: rgba(17, 17, 17, 1);
    --avtoriz-modal-toggle: rgba(17, 17, 17, 0.5);

        --amazon-logo: grayscale(100%);
     

   /* FONTS */

   --main-font: 'DM Sans', sans-serif;

}


:root[data-theme=dark] {

 --main-txt-color:  rgba(255, 255, 255,1);

 /*   */
    --pagination-back: rgba(17, 17,17,1);
    --pagination-next:rgb(234,198,69);
    --secondary-txt-color: rgb(243, 243, 243);
    --paragraph-color: rgb(180, 175, 175);

/*  */
    --hovertxt: rgba(234, 198, 69, 1);
    --btn-border:rgba(79, 46, 232, 1);
    --header-background: rgb(17, 17, 17);
    --category-name: rgba(255, 255, 255, 0.6);
    --background-body:  rgba(32, 32, 36, 1);
    --logo-txt: rgb(243, 243, 243);
    --shopping-list-border: rgba(79, 46, 232, 0.4);
    --scroll: rgba(17, 17, 17, 0.6);
    --modal-bgr: rgba(32, 32, 36, 1);
    --avtoriz-form: rgba(246, 246, 246, 1);
    --paragraph-white-font-color: #f6f6f6;
    --pagination-bkg: #f4f4f4;
    --avtoriz-modal-toggle: rgba(246, 246, 246, 0.5);  
    --scroll-line: rgba(31, 31, 35, 0.6);

    --amazon-logo: brightness(0)invert(1);
}